.footer {
    position: relative;
    background-color: #FFF5F6;
    padding-bottom: 20px;
    padding-top: 48px;
    @media screen and (min-width: 768px) {
        padding-top: 40px;
        padding-bottom: 8px;
    }
    @media screen and (min-width: 1280px) {
        padding-top: 120px;
    }
}
.footer-flex {
    margin-bottom: 40px;
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: space-between;
    }
}
.footer::after {
    display: block;
    position: absolute;
    content: "";
    left: 0;
    bottom: 78px;
    width: 100%;
    height: 1px;
    background-color: #E1E1E1;
    @media screen and (min-width: 768px) {
        bottom: 42px;
    }
}

.footer__text {
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-transform: uppercase;
    color: #FFA5BA;
    max-width: 232px;
    @media screen and (min-width: 768px) {
        font-weight: 400;
        font-size: 26px;
        line-height: 1.15;
        letter-spacing: 0.04em;
    }
    @media screen and (min-width: 1280px) {
        max-width: 474px;
        font-size: 30px;
        line-height: 1.13;
    }
}

.footer__text--add {
    display: block;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;
    margin-top: 16px;
    @media screen and (min-width: 768px) {
        margin-top: 12px;
        font-size: 48px;
        line-height: 1.15;
        letter-spacing: 0.06em;
    }
    @media screen and (min-width: 1280px) {
        margin-top: 16px;
        font-size: 58px;
        line-height: 1.14;
        max-width: 474px;
    }
}

.social__list {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 40px;
}
.social__item+.social__item {
    margin-left: 28px;
}

.social__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #FEA5BB;
    width: 32px;
    height: 32px;
    &:hover,
    &:focus {
        background-color: #D41443;
    }
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (min-width: 768px) {
        width: 34px;
        height: 34px;
    }
}

.social__icon {
    fill: #FFFFFF;
}


.footer__contact {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.186;
    text-align: right;
    color: #000000;
    margin-bottom: 40px;
    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.86;
    }
    @media screen and (min-width: 1280px) {
        font-size: 16px;
        line-height: 1.86;
        letter-spacing: 0.02em;
    }
}

.footer__phone-number {
    color: #D41443;
}
.site-sign {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    letter-spacing: 0.02em;
    text-align: center;
    color: #907E82;
    margin-right: auto;
    @media screen and (min-width: 768px) {
        line-height: 1.86;
    }
}
