
.section--big-padding-top{
    padding-top: 80px;
}

.product {
    background-color: $primaryColor;
}

.section-title-label {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;
    text-align: center;
    text-transform: uppercase;
    color: #FFA5BA;
    margin-bottom: 15px;
}

.product__title {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;
    margin-bottom: 8px;
}
.product__list{
   
}
.product__box{
    position: relative;
    padding-top: 140px;
}
.product__card {
   background: #FFB8CA;
   border-radius: 24px;
   padding: 160px 40px 40px 40px;
}

