.product__name{
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 1.15;
           
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
    
        color: $primaryColor;
        margin-bottom: 70px;
}


.product__description{
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.52;
     
        text-align: center;
        letter-spacing: 0.04em;
    
        color: $primaryColor;
        margin-bottom: 32px;
}

.button--round{
    display: flex;
    width: 40px;
    height: 40px;
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid transparent;
    background-color: $primaryColor;
    fill: $accentColor;
    justify-content: center;
    align-items: center;
    
  
        &:hover,
        &:focus {
            background-color: $accentColor;
            fill: $primaryColor;
        }

    transition: background-color $hooverDuration $hoverTimeFunction, 
    fill $hooverDuration $hoverTimeFunction;

}
